import React from "react";
import Layout from "./components/Layout";
import Registration from "./pages/auth/Registration";
import LoginPage from "./pages/auth/LoginPage";
import { Route, Routes } from "react-router-dom";
import HomePage from "./pages/HomePage";
import CrudPage from "./pages/CrudPage";
import UpdatePage from "./pages/UpdatePage";
import CreateDemoUserPage from "./pages/CreateDemoUserPage";
import PbRoute from "./pages/auth/Pbroute";
import Protectedroute from "./pages/auth/Protectedroute";
import PageNotFound from "./pages/PageNotFound";

const App = () => {
  return (
    <Layout>
      <Routes>
        <Route path="/" element={<HomePage />} />
        <Route
          path="/register"
          element={
            <PbRoute>
              <Registration />
            </PbRoute>
          }
        />
        <Route
          path="/login"
          element={
            <PbRoute>
              <LoginPage />
            </PbRoute>
          }
        />
        <Route
          path="/crudpage"
          element={
            <Protectedroute>
              {" "}
              <CrudPage />
            </Protectedroute>
          }
        />
        <Route
          path="/updatepage/:id"
          element={
            <Protectedroute>
              <UpdatePage />
            </Protectedroute>
          }
        />
        <Route
          path="/createdemouser"
          element={
            <Protectedroute>
              <CreateDemoUserPage />
            </Protectedroute>
          }
        />
        <Route
          path="*"
          element={
            <Protectedroute>
              <PageNotFound />
            </Protectedroute>
          }
        />
      </Routes>
    </Layout>
  );
};

export default App;
