import { message } from "antd";
import axios from "axios";
import React, { useEffect, useState } from "react";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import "../styles/PublicPages.css";

const UpdatePage = () => {
  const params = useParams();
  const navigate = useNavigate();
  const [puser, setPuser] = useState();
  const [name, setName] = useState("");
  const [address, setAddress] = useState("");
  const [education, setEducation] = useState("");
  const [age, setAge] = useState("");
  const [location, setLocation] = useState("");

  const getUser = async () => {
    try {
      const res = await axios.get(
        `${process.env.REACT_APP_API}/api/v1/user/fetsingle/${params.id}`,
        {
          headers: {
            authorization: localStorage.getItem("token"),
          },
        }
      );
      if (res.data.success) {
        message.success("user is fetch successfull");
        setPuser(res.data.data);
      }
    } catch (error) {
      console.log(error);
    }
  };
  useEffect(() => {
    getUser();
  }, []);

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      const res = await axios.put(
        `${process.env.REACT_APP_API}/api/v1/user/updateduser/${params.id}`,
       
        {
          name,
          address,
          education,
          age,
          location,
        },
        {
            headers: {
              authorization: localStorage.getItem("token"),
            },
          },
      );
      if (res.data.success) {
        message.success("data is updated");
        navigate("/crudpage");
      }
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <>
      <div className="update">
      <form className="u-form">
        <h3 className="text-center">Update User Here</h3>
        <div className="mb-3">
          <label htmlFor="exampleInputEmail1" className="form-label">
            Name
          </label>
          <input
            type="text"
            className="form-control"
            id="exampleInputEmail1"
            aria-describedby="emailHelp"
            value={name}
            placeholder={puser?.name}
            onChange={(e) => {
              setName(e.target.value);
            }}
          />
        </div>
        <div className="mb-3">
          <label htmlFor="exampleInputEmail1" className="form-label">
            Address
          </label>
          <input
            type="text"
            className="form-control"
            id="exampleInputEmail1"
            value={address}
            placeholder={puser?.address}
            onChange={(e) => {
              setAddress(e.target.value);
            }}
            aria-describedby="emailHelp"
          />
        </div>{" "}
        <div className="mb-3">
          <label htmlFor="exampleInputEmail1" className="form-label">
            education
          </label>
          <input
            type="text"
            className="form-control"
            id="exampleInputEmail1"
            aria-describedby="emailHelp"
            value={education}
            placeholder={puser?.education}
            onChange={(e) => {
              setEducation(e.target.value);
            }}
          />
        </div>{" "}
        <div className="mb-3">
          <label htmlFor="exampleInputEmail1" className="form-label">
            Age
          </label>
          <input
            type="text"
            className="form-control"
            id="exampleInputEmail1"
            aria-describedby="emailHelp"
            value={age}
            placeholder={puser?.age}
            onChange={(e) => {
              setAge(e.target.value);
            }}
          />
        </div>{" "}
        <div className="mb-3">
          <label htmlFor="exampleInputEmail1" className="form-label">
            Location
          </label>
          <input
            type="text"
            className="form-control"
            id="exampleInputEmail1"
            aria-describedby="emailHelp"
            placeholder={puser?.location}
            value={location}
            onChange={(e) => {
              setLocation(e.target.value);
            }}
          />
        </div>{" "}
        <button
          type="submit"
          className="btn btn-primary"
          onClick={handleSubmit}
        >
          Update
        </button>
      </form>
      </div>
    </>
  );
};

export default UpdatePage;
