import React, { useEffect, useState, useTransition } from "react";
import axios from "axios";
import "../styles/PublicPages.css";
import { Checkbox, Radio, message } from "antd";
import { useNavigate } from "react-router-dom";

const CrudPage = () => {
  let count = 0;
  const age = [
    {
      _id: 0,
      name: "$0 to 19",
      array: [0, 19],
    },
    {
      _id: 1,
      name: "$20 to 39",
      array: [20, 39],
    },
    {
      _id: 2,
      name: "$39 to 60",
      array: [39, 60],
    },
    {
      _id: 3,
      name: "$60 to 100",
      array: [60, 100],
    },
  ];
  const navigate = useNavigate();
  const [users, setUsers] = useState([]);
  const [radio, setRadio] = useState([]);
  const getAllDemoUsers = async () => {
    try {
      const res = await axios.get(
        `${process.env.REACT_APP_API}/api/v1/user/fetchall`,
        {
          headers: {
            authorization: localStorage.getItem("token"),
          },
        }
      );
      if (res.data.success) {
        message.success("users list fetch successfully");
        setUsers(res?.data);
      }
    } catch (error) {
      console.log(error);
    }
  };

  const handleDelete = async (value) => {
    try {
      const res = await axios.delete(
        `${process.env.REACT_APP_API}/api/v1/user/deleteduser/${value}`,
        {
          headers: {
            authorization: localStorage.getItem("token"),
          },
        }
      );
      if (res.data.success) {
        message.success("delete successful");
        window.location.reload();
        navigate("/crudpage");
      }
    } catch (error) {
      console.log(error);
    }
  };
  useEffect(() => {
    if (!radio) getAllDemoUsers();
  }, [radio]);
  useEffect(() => {
    if (radio) handleFilter();
  }, [radio]);

  const handleFilter = async () => {
    try {
      // window.alert(radio)
      const res = await axios.post(
        `${process.env.REACT_APP_API}/api/v1/user/filterAge`,
        { radio },
        {
          headers: {
            authorization: localStorage.getItem("token"),
          },
        }
      );
      if (res.data.success) {
        setUsers(res?.data);
      }
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <>
      {/* <h1>{radio}</h1> */}
      <div className="crud" >
        <div className="left-t">
          <h1 className="text-line">
            <button
              className="btn btn-primary"
              onClick={() => {
                navigate("/createdemouser");
              }}
            >
              create new user
            </button>
            <hr />

            <div className="">
              <h1 className="head-filter">Filter by age</h1>
              <Radio.Group onChange={(e) => setRadio(e.target.value)}>
                {age?.map((p) => (
                  <div key={p._id}>
                    <Radio value={p.array}>{p.name}</Radio>
                  </div>
                ))}
              </Radio.Group>
            </div>
          </h1>
        </div>
        {/* <div className="tt"> */}
          <table className="table-r">
            <>
              <thead>
                <tr>
                  <th>#</th>
                  <th>Name</th>
                  <th>Address</th>
                  <th>Age</th>
                  <th>Action</th>
                </tr>
              </thead>
              <tbody>
                {users?.data?.map((u) => (
                  <>
                    <tr className="t-row">
                      <td>{(count = count + 1)}</td>
                      <td>{u.name}</td>
                      <td>{u.address}</td>
                      <td>{u.age}</td>
                      <td>
                        {
                          <button
                            className="btn btn-info"
                            onClick={() => {
                              navigate(`/updatepage/${u._id}`);
                            }}
                          >
                            Update
                          </button>
                        }
                      </td>
                      <td>
                        {
                          <button
                            className="btn btn-danger"
                            onClick={() => handleDelete(u._id)}
                          >
                            Delete
                          </button>
                        }
                      </td>
                    </tr>
                  </>
                ))}
              </tbody>
            </>
          </table>
        {/* </div> */}
      </div>
    </>
  );
};

export default CrudPage;
